.new-products {
  position: relative;
  padding-bottom: 70px;

  &__header {
    text-align: center;
    margin-bottom: 60px;

    & h2 {
      margin: 0;
    }

    & a {
      position: relative;
      font-style: normal;
      font-weight: 500;
      font-size: 16px;
      line-height: 130%;
      /* or 21px */
      text-align: center;

      color: #1d1613;

      text-decoration: none;

      &:focus,
      &:hover {
        opacity: 0.5;
      }

      &::before {
        content: "";
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 1px;
        background-color: #a87b62;
      }
    }
  }
}
